.reviews__form {
  position: relative;

  max-width: 700px;
  width: 100%;
  margin: 0 auto;

  .form {
    display: flex;
    flex-direction: column;
    gap: 20px;

    &__fields {
      display: flex;
      flex-direction: column;
      gap: 20px;

      @include small-tablet {
        grid-template-columns: repeat(1, 1fr);
        gap: 20px;
      }
    }

    &__field {
      position: relative;
      display: flex;

      input,
      textarea {
        font-size: 20px;
        line-height: normal;
        padding: 10px;
        width: 100%;
        color: $white;
        border: 1px solid transparent;
        background-color: rgba(255, 255, 255, 0.1);

        &.error {
          border-color: $red;
        }
      }

      input {
        width: fit-content;
      }

      textarea {
        resize: none;
        min-height: 100px;
      }

      @include small-tablet {
        input,
        textarea {
          width: 100%;
          padding: 10px;
          font-size: 18px;
        }

        textarea {
          min-height: 100px;
        }
      }
    }

    &__btn {
      min-width: 200px;
    }

    &__result {
      @include flex-all-center;

      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      border: 1px solid $red;
      background-color: $bg;

      z-index: 1;
    }

    &__result-text {
      @include flex-all-center;
      flex-direction: column;
      gap: 10px;

      &::before {
        content: "";
        display: block;
        width: 40px;
        height: 40px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
